'use strict';


import React, { Component } from 'react'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'
import CreateRoleView from '../components/CreateRole'
import {actions } from '../modules/role'

const mapStateToProps = (state) => {
  return state.role;
};

const mapDispatchToProps = actions;

const changeHandlerMappings = {
  onChangeDescription : 'description',
  onChangeName : 'name'
};


class CreateRoleContainer extends Component {
  static propTypes = {
    name: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired
  };

  static defaultProps = {
    name: "",
    description: ""
  };

  handler = (propName, ev, i, newVal) => {
    if (i && !newVal) {
      newVal = i;
    }

    this.props.changeDraft({[propName]:newVal});
  };

  createRole = (ev) => {
    this.props.createRole();
  };

  render () {
    let {name, description} = this.props;
    let passedHandlers = {};
    Object.keys(changeHandlerMappings).forEach(key => {
      passedHandlers[key] = this.handler.bind(this, changeHandlerMappings[key]);
    });
    passedHandlers.onCreate = this.createRole;

    return <CreateRoleView  {...passedHandlers} {...this.props} />
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(CreateRoleContainer)



