'use strict';
import React, { Component } from 'react';
import PropTypes from 'prop-types'

import { connect } from 'react-redux'
import { actions } from '../modules/roles'
import { actions as roleActions}  from '../modules/role';

import RolesTable from '../components/RolesTable';
import CreateRoleContainer from './CreateRoleContainer';

import '../components/RolesLayout.scss';

const mapDispatchToProps = {
  ...actions,
  ...roleActions
};

const mapStateToProps = (state) => {
  return {
    ...state.roles
  }
};

class RolesContainer extends Component {
  static propTypes = {
    roles: PropTypes.array.isRequired,
    isRequesting: PropTypes.bool.isRequired,
    hasReceived: PropTypes.bool.isRequired,
    errors: PropTypes.any.isRequired
  }

  onDeleteRole = (ev)=>{
    this.props.deleteRoles(this.props.selectedRoleIds);
  }

  componentDidMount() {
    if (!this.props.isRequesting && !this.props.hasReceived) {
      this.props.getRoles();
    }
  }

  render () {

    return <div className="roles-layout">
      <div>
        <CreateRoleContainer />
      </div>
      <div>
        <RolesTable {...this.props} />
      </div>
    </div>
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(RolesContainer)


