'use strict';


import React from 'react';
import PropTypes from 'prop-types'

import Subheader from 'material-ui/Subheader'
import TextField from 'material-ui/TextField'
import Paper from 'material-ui/Paper'
import RaisedButton from 'material-ui/RaisedButton'
import Divider from 'material-ui/Divider'
import SelectField from 'material-ui/SelectField'
import MenuItem from 'material-ui/MenuItem';
import Snackbar from 'material-ui/Snackbar';

import './CreateRole.scss'

const paperStyle = {
  padding: '1rem'
}
let CreateRoleView = (props) => {
  let openSnackbar = !!(props.isSaved && props.id);
  let snackbarMessage =  openSnackbar ? `${props.hasSaved} has been saved` : '';
  let buttonLabel = (props.isSaving) ? 'Saving...' : 'Save';
  let className = (props.isSaving) ? 'create-role-view state-saving' : 'create-role-view';


return <Paper className={className} style={paperStyle}>
    <h2>Create Role</h2>
    <div>
    <TextField floatingLabelText="Role Name"
                 disabled={props.isSaving}
                 onChange={props.onChangeName}
                 value={props.name}/>
    </div>
    <div>
      <TextField floatingLabelText="Description [optional]"
                 disabled={props.isSaving}
                 multiLine={true}
                 rows={5}
                 onChange={props.onChangeDescription}
                 value={props.description}/>
    </div>
    <div>
      <RaisedButton label="Save" onTouchTap={props.onCreate} />
    </div>
    <Snackbar bodyStyle={{ height: 100, lineHeight: '32px', flexGrow: 0 }}
              open={openSnackbar}
              message={snackbarMessage}
              autoHideDuration={4000} />
  </Paper>
};
export default CreateRoleView;


