'use strict';

import React, {Component} from 'react';
import PropTypes from 'prop-types'
import RoleRow from './RoleRow'

import './RolesTable.scss';

class RolesTableView extends Component {

  isSelected = (id) => {
    let selected = this.props.selectedRoleIds.indexOf(id) !== -1;
    return selected;
  }

  onItemSelect = (id) => (evt, selected) => {
    let selectedIds = [].concat(this.props.selectedRoleIds);
    if (selected) {
      selectedIds.push(id);
    } else {
      selectedIds.splice(selectedIds.indexOf(id), 1);
    }

    this.props.selectRoles(selectedIds);
  }

  render() {
    let {props, onItemSelect} = this;
    let rows = props.roles.map((role, i) => {
      let {id} = role;
      let selected = this.isSelected(id);
      return <RoleRow key={`role-table-row-${id}`} {...role} selected={selected} onSelect={onItemSelect(id)} />
    });

    return <table className="roles-list">
      <thead>
        <tr>
          <th></th>
          <th>Name</th>
          <th>Description</th>
        </tr>
      </thead>
      <tbody>
        {rows}
      </tbody>
    </table>
  }
}

RolesTableView.defaultProps = {
  roles: [],
  errors: false,
  selectedRoleIds : []
}

RolesTableView.propTypes = {
  roles: PropTypes.array.isRequired,
  hasReceived: PropTypes.bool,
  isRequesting: PropTypes.bool,
  errors : PropTypes.any.isRequired,
  selectedRoleIds : PropTypes.array.isRequired,
  selectRoles : PropTypes.func.isRequired
}

export default RolesTableView;




