'use strict';

const DEFAULT_STATE = {
  isSaved: false,
  isSaving: false,
  errors: false
}

import client from '../../../helpers/ApiClient';
import {Enum} from 'enumify';
import _pick from 'lodash/pick';
import moment from 'moment';
import ms from 'ms';

// ------------------------------------
// Constants
// ------------------------------------
import ACTION_TYPES from '../../../actionTypes/roles';

const createRole = function () {
  /*  This is a thunk, meaning it is a function that immediately
   returns a function for lazy evaluation. It is incredibly useful for
   creating async actions, especially when combined with redux-thunk! */

  return (dispatch, getState) => {
    let role = getState().role;

    dispatch({
      type: ACTION_TYPES.CREATING_ROLE,
      role
    });

    return client.post('/Roles', {data:role})
      .then(r => {
        // get it, since the api doesn't return the relational data in the post response
        return client.get(`/Roles/${r.id}`);
      }).then(r => {
        dispatch({
          type: ACTION_TYPES.CREATED_ROLE,
          role:r
        });

        // reset state after 4s
        setTimeout(()=>{
          dispatch({
            type: ACTION_TYPES.CHANGE_DRAFT_ROLE,
            changes: {}
          });
        }, 4000);
      }).catch(err => {
        // TODO - check for validation errors
        dispatch({
          type: ACTION_TYPES.CREATED_ROLE_ERROR,
          err
        });
      });
  }
};

/**
 * @param {Object} changes
 */
const changeDraft = function (changes) {
  return (dispatch, getState) => {
    dispatch({
      type: ACTION_TYPES.CHANGE_DRAFT_ROLE,
      changes
    })
  }
};

export const actions = {
  changeDraft,
  createRole
};

export default function reducer(state = DEFAULT_STATE, action) {
  let newState = state;
  switch(action.type) {
    case ACTION_TYPES.CHANGE_DRAFT_ROLE:
      newState = Object.assign({}, state, action.changes, {
        isSaved:false,
        isSaving:false,
        errors: false
      });
      delete newState.id;
      return newState;
    case ACTION_TYPES.CREATING_ROLE:
      newState = Object.assign({}, state, {
        isSaved:false,
        isSaving:true
      });
      return newState;
    case ACTION_TYPES.CREATED_ROLE:
      // TODO - map response to state
      newState = Object.assign({}, DEFAULT_STATE, {
        isSaved:true,
        isSaving:false
      });
      return newState;
    case ACTION_TYPES.CREATED_ROLE_ERROR:
      newState = Object.assign({}, state, {
        isSaving:true,
        errors:action.err
      });
      return newState;      // TODO - validation errors!

    default:
      return newState;
  }

};




